.helo--btn {
  gap: 5px;
  display: inline-flex;
  align-items: center;
}

.helo--btn-wrapper svg,
.helo--btn svg {
  width: 1em;
  height: 1em;
}

/* Button One */
.btn-border-divide {
  align-items: flex-end;
  overflow: hidden;
}

.btn-border-divide .text,
.btn-border-divide .icon {
  border-bottom: 1px solid;
}

.btn-border-divide .icon {
  display: inline-flex;
  padding-bottom: 3px;
  position: relative;
}

.btn-border-divide .icon i:first-child,
.btn-border-divide .icon svg:first-child {
  position: absolute;
  transform: translate(-10px, 10px);
  transition: all 0.3s;
  opacity: 0;
}

.btn-border-divide .icon i:last-child,
.btn-border-divide .icon svg:last-child {
  transition: all 0.3s;
  opacity: 1;
}

/* Hover */
.btn-border-divide:hover .icon i:first-child,
.btn-border-divide:hover .icon svg:first-child {
  transform: translate(0, 0);
  opacity: 1;
}

.btn-border-divide:hover .icon i:last-child,
.btn-border-divide:hover .icon svg:last-child {
  transform: translate(10px, -10px);
  opacity: 0;
}

/* Button Flip */
.wc-btn-wrapper {
  display: inline-block;
}

.btn-text-flip svg,
.wc-btn-wrapper svg {
  width: 1em;
  height: 1em;
}

.btn-text-flip {
  perspective: 1000px;
}

.btn-text-flip:hover span {
  transform: rotateX(90deg) translateY(-12px);
}

.btn-text-flip span {
  position: relative;
  display: inline-block;
  padding: 0;
  color: #121212;
  transition: transform 0.3s;
  transform-origin: 50% 0;
  transform-style: preserve-3d;
}

.btn-text-flip span:before {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 100%;
  content: attr(data-text);
  transition: color 0.3s;
  transform: rotateX(-90deg);
  transform-origin: 50% 0;
  text-align: center;
}

/* Style 4 */
.style-4 .wc-btn-primary {
  display: inline-flex;
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  color: var(--white);
  background-color: var(--primary);
  padding: 18px 42px;
  border-radius: 5px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.style-4 .wc-btn-primary span {
  position: absolute;
  width: 0;
  height: 0;
  background: #121212;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.5s ease;
  z-index: -1;
}

.style-4 .wc-btn-primary strong {
  width: 50px;
  display: inherit;
  height: 1px;
  background: #fff;
  margin-left: 5px;
  position: relative;
}

.style-4 .wc-btn-primary strong::after {
  position: absolute;
  content: "";
  top: -5px;
  right: 30px;
  border: solid var(--white);
  border-width: 0 1px 1px 0;
  padding: 5px;
  transform: rotate(-45deg);
  transition: all 0.3s;
  opacity: 0;
}

.style-4 .wc-btn-primary:hover span {
  width: 500px;
  height: 500px;
}

.style-4 .wc-btn-primary:hover strong::after {
  right: 0;
  opacity: 1;
}

.btn-text-flip {
  align-items: center;
}

/* Button 1 and 2 */
.wc-btn-group {
  display: flex;
  width: fit-content;
}

.wc-btn-group .wc-btn-primary {
  padding: 21px 40px;
  font-weight: 600;
  font-size: 16px;
  line-height: 1;
  color: #fff;
  background-color: #0A1A3D;
  text-transform: capitalize;
  transition: all 0.3s;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  z-index: 1;
  gap: 30px;
  white-space: nowrap;
  border-radius: 30px;
  text-decoration: none;
}

.wc-btn-group .wc-btn-play {
  color: #fff;
  fill: #fff;
  border-radius: 50px;
  background-color: #0A1A3D;
  border: 1px solid transparent;
  height: 60px;
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s;
}

.wc-btn-group > *:nth-child(2) {
  transform: scale3d(1, 1, 1);
}

.wc-btn-group:hover > *:nth-child(2) {
  transform: scale3d(1, 1, 1);
}

/* Style 1 */
.style-5 .wc-btn-group > *:nth-child(1) {
  transform: scale3d(0.5, 0.5, 1);
  margin-right: calc(var(--icon-width, 60px) * -1);
  opacity: 0;
}

.style-5 .wc-btn-group > *:nth-child(3) {
  transform: scale3d(1, 1, 1);
  margin-left: 0;
  opacity: 1;
}

.style-5 .wc-btn-group:hover > *:nth-child(1) {
  transform: scale3d(1, 1, 1);
  margin-right: 0;
  opacity: 1;
}

.style-5 .wc-btn-group:hover > *:nth-child(3) {
  transform: scale3d(0.5, 0.5, 1);
  margin-left: calc(var(--icon-width, 60px) * -1);
  opacity: 0;
}

/* Style 2 */
.style-6 .wc-btn-group > *:nth-child(1) {
  transform: scale3d(1, 1, 1);
  margin-right: 0;
  opacity: 1;
}

.style-6 .wc-btn-group > *:nth-child(3) {
  transform: scale3d(0.5, 0.5, 1);
  margin-left: calc(var(--icon-width, 60px) * -1);
  opacity: 0;
}

.style-6 .wc-btn-group:hover > *:nth-child(1) {
  transform: scale3d(0.5, 0.5, 1);
  margin-right: calc(var(--icon-width, 60px) * -1);
  opacity: 0;
}

.style-6 .wc-btn-group:hover > *:nth-child(3) {
  transform: scale3d(1, 1, 1);
  margin-left: 0;
  opacity: 1;
}